<template>
    <a-row class="bg">
        <a-col>
            <a-row type="flex" justify="space-around" align="middle">
                <a-col :span="12">
                    <div class="title">{{itemData.symbol}}</div>
                </a-col>
                <a-col :span="12" class="t-right"><span :class="statusTextStyle">{{itemData.statusText}}</span></a-col>
            </a-row>
        </a-col>
        <a-col>
            <hr>
            <a-row>
                <a-col :span="6">
                    <img :src="itemData.icon" :alt="itemData.symbol" height="64">
                </a-col>
                <a-col>
                    <a-row>
                        <a-col>
                            <a-col :span="7">Exchange Ratio</a-col>
                            <a-col>1:{{1/itemData.usdt_exchange}}</a-col>
                        </a-col>
                        <a-col>
                            <a-col :span="7">Start Time</a-col>
                            <a-col>{{itemData.start_time}}</a-col>
                        </a-col>
                        <a-col>
                            <a-col :span="7">End Time</a-col>
                            <a-col>{{itemData.end_time}}</a-col>
                        </a-col>
                        <a-col style="padding-top:20px;">
                            <a-button :disabled="disabled" @click="onClickItem(itemData)" type="primary" block>view details</a-button>
                        </a-col>
                    </a-row>
                </a-col>
            </a-row>
        </a-col>
    </a-row>
</template>

<script>
export default {
    name: "StartupListItem",
    components: {},
    props: ['itemData'],
    data() {
        return {
            disabled: true,
            statusTextStyle: 'hui',
        };
    },
    methods: {
        onClickItem(itemData) {
            this.$router.push(`/startup-item?name=${itemData.symbol.toLocaleLowerCase()}`)
        },
        init() {
            this.disabled = this.itemData.status == 2 ? false : true;
            if (this.itemData.status == 2) {
                this.disabled = false;
                this.statusTextStyle = 'lv'
            } else if (this.itemData.status == 3) {
                this.statusTextStyle = 'hong'
            }
        },
    },
    mounted() {
        this.init();
    },
};
</script>

<style scoped>
.bg {
    background-color: #161f37;
    margin-bottom: 10px;
    margin-right: 10px;
    border-radius: 3px;
    padding: 10px;
    text-align: left;
    float: left;
    overflow: hidden;
    width: 45%;
    color: #ffffff;
    min-width: 360px;
}
.title {
    font-size: 1.25rem;
}
.hui {
    color: #999999;
}
.hong {
    color: #f00;
}
.lv {
    color: #00aa00;
}
</style>