<template>
    <div class="startup">

        <a-spin :spinning="loading">
            <div class="spin-content">

                <a-row class="t-left">
                    <a-col :span="this.g.container.span" :offset="this.g.container.offset">
                        <div class="mt30"></div>
                        <div class="starting-project">Starting project</div>
                    </a-col>
                </a-row>

                <div class="mt30"></div>

                <a-row>
                    <a-col :span="this.g.container.span" :offset="this.g.container.offset">
                        <Item v-for="item in list" :key="item.name" :itemData="item" />
                    </a-col>
                </a-row>

                <a-row>
                    <div class="mt30"></div>
                </a-row>

            </div>
        </a-spin>

    </div>
</template>


<script>
import Item from "@/components/Startup/ListItem.vue";
export default {
    name: "Startup",
    components: {
        Item
    },
    data() {
        return {
            loading: false,
            list: []
        };
    },
    methods: {
        init() {
            this.loading = true;
            $.get(`${this.g.apiUrl}/api/platform-currency`, this.pageParameters, (res) => {

                this.loading = false;

                let a = res.data.list
                a.forEach(function (item) {
                    item.start_time = moment(item.start_time).format("YYYY-MM-DD HH:mm")
                    item.end_time = moment(item.end_time).format("YYYY-MM-DD HH:mm")

                    let time = new Date().getTime()
                    let start = new Date(item.start_time).getTime()
                    let end = new Date(item.end_time).getTime()
                    if (time < start) {
                        item.status = 1
                        item.statusText = 'has not started'
                    } else if (time > end) {
                        item.status = 3
                        item.statusText = 'over'
                    } else if (time > start) {
                        item.status = 2
                        item.statusText = 'ongoing'
                    } else {
                        item.status = 0
                        item.statusText = '-'
                    }


                })
                this.list = a
            }).fail(err => {
                this.loading = false;
                console.log(err)
            })
        },
    },
    mounted() {
        this.init();
    },
};
</script>


<style scoped>
.startup {
    background-color: #232c48;
    font-family: "Courier New", Courier, monospace;
}
.mt30 {
    margin-top: 30px;
}
.starting-project {
    padding: 10px;
    color: #eeeeee;
    background-color: #161f37;
}
</style>